
.slider {
	overflow: hidden; }
.slider__slide {
	@include css-lock(400, 670, 991, 1599, "min-height");
	@include cover;
	position: relative;
	@include r(639) {
		min-height: 250px; } }
.slider__content {
	text-align: center;
	display: table;
	@include css-lock(400, 670, 991, 1599, "min-height");
	width: 100%;
	@include r(639) {
		min-height: 250px; } }

.slick-prev, .slick-next {
	top: auto;
	bottom: 30px;
	z-index: 1;
	@include r(991) {
		bottom: 27px; } }
.slick-prev {
	@include s(prev);
	left: 50%;
	margin-left: -40px; }
.slick-next {
	@include s(next);
	right: 50%;
	margin-right: -40px; }
