.topline {
	padding: 10px 0;
	background-color: #242424;
	width: 100%;
	@include r(991) {
		padding:  9px 0; }
	@include r(767) {
		padding:  11px 0; } }
.topline__wrapper {
	padding: 0 17px;
	width: 100%;
	position: relative;
	@include clr;
	@include r(1420) {
		padding: 0; } }
.topline__left {
	float: left;
	width: 150px;
	margin-top: 3px;
	@include r(1420) {
		width: 130px; }
	@include r(991) {
		width: 200px; } }
.topline__middle {
	@include vcenter;
	@include r(1420) {
		left: 40%; }
	@include r(991) {
		display: none; } }
.topline__right {
	float: right;
	text-align: right;
	display: table; }
.search-block {
	display: inline-block;
	position: relative;
	width: 100%; }
.topline__search-block {
	margin-top: 4px; }
.search-block__input {
	@include placeholder-color($silver);
	font: $topline-font;
	color: $silver;
	background: transparent;
	padding: 5px 0px 5px 18px;
	width: 100%; }
.search-block__button {
	width: 14px;
	height: 14px;
	@include cover;
	display: block;
	position: absolute;
	top: 50%;
	transform: translateY(-50%); }
.topline__list {
	max-width: 900px; }
.topline__list-item {
	display: inline-block;
	margin: 0 15px;
	@include r(1420) {
		margin: 0 5px; } }
.topline__list-link {
	@extend .grayLinkStyle;
	@include r(1199) {
		font-size: 12px; } }
.topline__marker {
	width: 25px;
	height: 19px;
	@include cover;
	background-size: contain;
	display: inline-block;
	vertical-align: bottom; }
.topline__marker-bag {
	width: 17px;
	height: 19px; }
.topline__marker-car {
	width: 25px;
	height: 16px; }
.topline__marker-reload {
	width: 16px;
	height: 16px; }
.topline__marker-certif {
	width: 20px;
	height: 18px; }
.phone-callback {
	width: 150px;
	text-align: right;
	@include r(767) {
		display: none; } }
.topline__phone-callback {
	margin-top: 3px;
	margin-right: 12px; }
.authorization-block {
	width: 151px;
	display: table-cell;
	vertical-align: middle;
	position: relative;
	&:before, &:after {
		content: '';
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: 1px;
		height: 34px;
		background-color: #363636; }
	&:before {
		left: 0; }
	&:after {
		display: none; }
	@include r(991) {
		&:after {
			display: block;
			right: -20px; } }
	@include r(767) {
		display: none; } }
.phone {
	color: $white;
	font: 400 18px/1em $PR;
	span {
		font-weight: 600; } }
.callback {
	color: $pink;
	font: 600 13px/1em $PR;
	text-decoration: underline;
	display: block;
	vertical-align: top;
	&:hover {
		text-decoration: none; } }
.login, .registration {
	font: 400 17px/1.3em $PR;
	letter-spacing: -1px; }
.login {
	color: $white;
	margin-right: 8px; }
.registration {
	color: $gray-darken; }
.menu-btn {
	display: none;
	@include r(991) {
		margin-left: 38px;
		display: inline-block;
		vertical-align: top;
		position: relative;
		top: 3px;
		width: 28px;
		height: 20px;
		overflow: visible;
		&:before, &:after {
			transition: all .3s;
			content: '';
			position: absolute;
			height: 3px;
			width: 28px;
			background: white;
			left: 0; }
		&:before {
			top: 0px;
			transform-origin: top left; }
		&:after {
			bottom: 0px;
			transform-origin: bottom left; }
		div {
			height: 3px;
			width: 28px;
			z-index: 2;
			background: white;
			position: absolute;
			top: 50%;
			margin-top: -2px;
			transition: all .2s; } }
	@include r(767) {
		margin-left: 0; } }
.menu-btn-is-active {
	@include r(991) {
		div {
			opacity: 0; }
		&:before, &:after {
			width: 26px; }
		&:before {
			transform: translateX(6px) rotate(45deg); }
		&:after {
			transform: translateX(6px) rotate(-45deg); } } }

