.mobnav-main {
	margin-top: 5px; }
.mobnav-main__item {
	border-bottom: 1px solid #242424;
	padding: 21px 0;
	&:last-of-type {
		position: relative;
		border-color: #fff;
		&:before {
			content: '';
			position: absolute;
			top: -1px;
			left: 0;
			width: 100%;
			height: 1px;
			background: $pink; } }
	@include r(560) {
		padding: 10px 0; } }
.mobnav-main__link {
	color: $white;
	font: 400 26px $PR;
	text-transform: uppercase;
	@include css-lock(16, 26, 560, 639); }
.mobnav-main__link--pink {
	color: $pink; }
.mobnav-second__item {
	padding: 16px 0;
	position: relative;
	&:after {
		content: '';
		position: absolute;
		bottom: 0px;
		left: 50%;
		transform: translateX(-50%);
		width: 66%;
		height: 1px;
		background: #242424; }
	@include r(560) {
		padding: 10px 0; } }
.mobnav-second__link {
	color: $gray;
	font: 400 18px $PR;
	@include css-lock(14, 18, 560, 639); }
.mobnav-second {
	margin-bottom: 46px;
	@include r(560) {
		margin-bottom: 20px; } }
.nav-responsive__phone {
	display: inline-block;
	margin-bottom: 9px;
	font-size: 32px;
	@include css-lock(20, 32, 560, 639); }
.nav-responsive__callback {
	@include css-lock(14, 18, 560, 639); }
