.about-us {
	text-align: center;
	@include css-lock(65, 90, 992, 1599, "margin-top"); }
.about-us, .about-us__inner {
	border-top: 1px solid #eaeaea; }
.about-us__article {
	max-width: 1145px;
	margin: 0 auto;
	color: $gray-light;
	font: 400 17px/1.6em $PR;
	@include css-lock(13, 41, 992, 1599, "margin-top");
	@include r(991) {
		font-size: 14px;
		padding:  0 20px; }
	@include r(767) {
		padding: 0; }
	@include r(650) {
		margin-top: 0; }
	p {
		@include rmin(769) {
			display: inline; } } }
.about-us__inner {
	@include css-lock(54, 102, 992, 1599, "margin-top");
	@include r(560) {
		margin-top: 30px; } }
.about-us__ul {
	position: relative;
	margin-top: -1px; }
.about-us__li {
	display: inline-block;
	position: relative;
	margin-left: -5px;
	&:last-of-type {
		a {
			border: none; } }
	@include r(991) {
		display: block;
		margin-left: 0; }
	a {
		display: block;
		padding: 34px 22px;
		color: #686868;
		font: 400 22px/1em $PR;
		transition: all .3s;
		text-transform: uppercase;
		border-right: 1px solid #eaeaea;
		&:hover {
			background: $pink;
			color: $white; }
		@include r(1200) {
			padding: 24px 12px; }
		@include r(991) {
			border-right: none;
			border-bottom: 1px solid #eaeaea;
			padding: 38px 12px; }
		@include r(560) {
			padding: 20px 12px;
			font-size: 16px; } } }

