.catalog {
	//padding-top: 80px
	@include css-lock(25, 80, 992, 1599, "padding-top");
	@include r(991) {
		padding-top: 24px; } }
.catalog__h3 {
	@include css-lock(46, 68, 992, 1599, "margin-bottom"); }
.product__h4 {
	transition: color .3s;
	//margin-top: 23px
	@include css-lock(0, 23, 992, 1599, "margin-top");
	@include css-lock(20, 30, 992, 1599);
	@include r(991) {
		margin-top: 23px;
		font-size: 30px; }
	@include r(560) {
		font-size: 20px;
		margin-top: 0px; } }
.product {
	//width: 484px
	display: inline-block;
	//height: 484px
	border: 1px solid #f1f1f1;
	margin-bottom: 58px;
	position: relative;
	//padding-top: 55px
	@include css-lock(300, 484, 992, 1599, "width");
	@include css-lock(300, 484, 992, 1599, "height");
	@include css-lock(10, 55, 992, 1599, "padding-top");
	transition: all .3s;
	&:hover {
		border-color: $pink;
		.product__h4 {
			color: $pink; } }
	@include r(991) {
		width: 484px;
		height: 484px;
		padding-top: 55px; }
	@include r(560) {
		padding-top: 10px;
		height: 245px; }
	a {
		display: block;
		height: 100%; } }
.product__img {
	width: 448px;
	margin: 0 auto;
	height: 335px;
	@include cover;
	background-size: contain;
	@include css-lock(270, 448, 992, 1599, "width");
	@include css-lock(240, 335, 992, 1599, "height");
	@include r(991) {
		width: 448px;
		height: 335px; }
	@include r(560) {
		width: 240px;
		height: 200px; } }
